@import "../node_modules/bulma/bulma.sass";
@import "./fonts/nunito/Nunito-Light.ttf";
@import "./fonts/nunito/Nunito-Bold.ttf";
@import "./fonts/nunito/Nunito-Italic.ttf";
@import "./fonts/Muli/Muli-Light.ttf";
@import "./fonts/Muli/Muli-Bold.ttf";
@import "./fonts/roboto/Roboto-Light.ttf";
@import "./fonts/roboto/Roboto-Bold.ttf";
@import "./pages/home/home";
@import "./components/header/header";
@import "./components/menu-nav/menu-nav";
@import "./components/testimonials/testimonials";
@import "./components/footer/footer";
@import "./pages/about/about";
@import "./pages/services/services";
@import "./pages/projects/projects";
@import "./pages/contact/contact";
@import "./pages/price-guide/price-guide";

*,
*::after,
*::before {
  box-sizing: border-box;
}
html,
body {
  font-size: 18px;
  max-width: 1440px;
  margin: 0 auto;
}

.app {
  margin: 0 20%;
  @include tablet {
    margin: 0 10%;
  }
  @include mobile {
    margin: 0;
  }
  .page {
    margin-bottom: 30px;
    .main-company-info {
      background: $color1;
    }
    .testimonials-component {
      .carousel-track,
      .carousel-dots {
        background: $color1;
      }
    }
    .profile {
      .description {
        background: $color1;
      }
    }
    h1 {
      color: $primary;
      margin-bottom: 24px;
      text-align: center;
    }
    h3 {
      text-align: center;
      color: $primary;
    }
  }
  .footer-wrap {
    background: $color3;
    color: white;
    p {
      color: white;
    }
  }

  // .header-wrap {
  //   border: 1px solid yellow;
  // }
  // .content-wrap {
  //   border: 1px solid orchid;
  // }
  // .footer-wrap {
  //   border: 1px solid purple;
  // }
}

//menu-nav burger display none on bigger devices than phone
.is-hidden {
  display: none;

  $break: 640px;
  @mixin menu-nav-bp {
    @media (min-width: #{$break}) {
      @content;
    }
  }
  @include menu-nav-bp() {
    display: none;
  }
}

.multiline {
  white-space: pre-wrap;

  @include mobile() {
    font-size: 12px;
  }
}

//custom font-weight
//Nunito
@font-face {
  font-family: "Nunito-Light";
  src: url("fonts/nunito/Nunito-Light.ttf");
  font-weight: normal;
}

@font-face {
  font-family: "Nunito-Bold";
  src: url("fonts/nunito/Nunito-Bold.ttf");
  font-weight: bold;
}

